"use client";
import { sideBarI } from "../config/sidebarConfig";
import Link from "next/link";
import { Menu } from "lucide-react";
import { Sheet,SheetTrigger } from "./ui/sheet";
interface sidebarProps {
  role: keyof typeof sideBarI;
}

export default function Sidebar({ role }: sidebarProps) {
  const items =
    sideBarI[role] ||
    []; /*is a concise way to safely access sidebar items based on the user's role */

  return (
    <aside className="bg-red-500 w-60 h-full fixed top-16">
      <div className="text-white text-4xl mt-6 ml-4">{role} </div>
      <div className="text-2xl ml-2 mt-3">superPlanification</div>
    </aside> /* when i nedd a sidebar i should use aside ,it's better then div here */
    /* By default, if you don’t specify a height or put content inside, the <aside> won’t show up. */
  );
}

